import React, { Component } from "react";
import styled from "styled-components";
import imdb from "../resources/icons/fa-imdb.svg";
import {
  defaultMovie,
  urlPrefix,
  posterUrlPrefix,
  imdbPrefix
} from "../resources/defaults";

const StyledMoviePage = styled.div`
  padding: 1em;
  display: flex;
  flex-flow: row wrap;
  align-items: space-around;
  width: 100%;
`;

const MoviePageContent = styled.div`
  padding: 0 1em;
  max-width: 50%;
`;

const MoviePageIcon = styled.img`
  filter: invert(100%);
  margin: 2em 1em;
  width: 50px;
`;

const GenreTag = styled.span`
  color: black;
  font-size: 0.8em;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 1em;
  padding: 0.4em 1em;
  margin: 0.2em;
  white-space: nowrap;
`;

const Poster = styled.img`
  max-height: 90vh;
`;

const MoviePageIconLink = styled.a`
  width: 50px;
  display: block;
`;

export default class MoviePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      movie: defaultMovie
    };
  }

  componentDidMount() {
    const movieUri =
      urlPrefix +
      this.props.match.params.id +
      "?api_key=1a60a9483b15c60fdebc9600bc1e67af&language=en-US";

    fetch(movieUri)
      .then(data => data.json())
      .then(data => {
        this.setState({ movie: data });
      });
  }

  renderGenreTags = () => {
    const { genres } = this.state.movie;
    return genres.map(({ name }) => <GenreTag>{name}</GenreTag>);
  };

  renderMovieTagline = () =>
    this.state.movie.tagline ? <h3>"{this.state.movie.tagline}"</h3> : ``;

  render() {
    const {
      poster_path,
      title,
      release_date,
      overview,
      imdb_id
    } = this.state.movie;
    return (
      <StyledMoviePage>
        <Poster src={posterUrlPrefix + poster_path} />

        <MoviePageContent>
          <h1>{title}</h1>
          {this.renderMovieTagline()}
          <h4>Release Date: {release_date}</h4>
          <p>{overview}</p>
          <h2>Genres</h2>
          {this.renderGenreTags()}
          <MoviePageIconLink href={imdbPrefix + imdb_id}>
            <MoviePageIcon src={imdb} />
          </MoviePageIconLink>
        </MoviePageContent>
      </StyledMoviePage>
    );
  }
}
