import React, { Component } from "react";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

const StyledNavbar = styled.div`
  height: 3em;
  width: 100%;
  position: fixed;
  top: 0;
  display: inline-block;
  color: red;
  background-color: #141414;
  z-index: 100;
`;

const Title = styled.div`
  float: left;
  height: 100%;
  display: grid;
  align-content: center;
`;

const TitleLink = styled(NavLink)`
  color: red;
  text-decoration: none;
  display: block;
`;

const TitleText = styled.h1`
  margin: 0;
`;

const StyledNavLink = styled(NavLink)`
  color: red;
  text-decoration: none;
  margin: 0em, 1em;
  padding: 0.8em;
`;

const NavbarLinks = styled.div`
  float: left;
  padding: 0.9em 1em;
`;

const SearchBar = styled.div`
  margin: 0.85em;
  width: 10em;
  float: right;
  color: grey;
`;

const NavbarSpacing = styled.div`
  height: 45px;
`;

export default class Navbar extends Component {
  constructor(props) {
    super(props);

    this.state = { searchTerm: "" };
  }

  handleChange = e => {
    this.setState({ searchTerm: e.target.value });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.onSubmit(this.state.searchTerm, this.props);
    this.setState({ searchTerm: "" });
  };

  render() {
    return (
      <React.Fragment>
        <StyledNavbar>
          <Title>
            <TitleLink to="/movie-app/" className="titleLink">
              <TitleText>Notflix</TitleText>
            </TitleLink>
          </Title>
          <NavbarLinks>
            <StyledNavLink to="/movie-app/NewReleases">
              New Releases
            </StyledNavLink>
            <StyledNavLink to="/movie-app/TopRated">Top Rated</StyledNavLink>
            <StyledNavLink to="/movie-app/ComingSoon">
              Coming Soon
            </StyledNavLink>
            <StyledNavLink to="/movie-app/HireMe">Hire Me!</StyledNavLink>
          </NavbarLinks>
          <SearchBar>
            <form onSubmit={this.handleSubmit}>
              <input
                type="text"
                placeholder="Search"
                value={this.state.searchTerm}
                onChange={this.handleChange}
              />
            </form>
          </SearchBar>
        </StyledNavbar>
        <NavbarSpacing />
      </React.Fragment>
    );
  }
}
